import React from 'react';

const Footeritem = ({footertitle}) => (
  <footer className='center'>
  {footertitle.map(e=>
      <span key={e.name}{...e}>{e.name}<a href="#"><span className='footer2'>{e.email}</span><a href='javascript:window.print(); void 0;'><img  src={e.link} className={e.class}/></a></a></span>)
  }
  
  </footer>
)
export default Footeritem




