import React from 'react';
import PaymentItem from './Payment-item';

 const Payment = ( {arraybank, onBankClick } ) => (
  <div>
      {arraybank.map(bank => 
      <div>
         <PaymentItem className="prueba"
            key = {bank.payment}
            onClick = {() => onBankClick(bank)}
            {...bank}
         />
      </div>
      )} 
  </div>
  ) 

export default Payment



