import ButtonPay2 from './Button-pay2';

const Main2 =() =>(
    <div>
        <Operation/>
        <div class="payment2">
        <Payment/>
        </div>
        <div class="intructionpay">
        <InstructionPay />
        </div>
        <ButtonPay2/>
    </div>
)
export default Main2


