import React from 'react';

const PaymentItem = ({ payment, link,textpe, onClick }) => (
  <a onClick={onClick}>
   <div >
           <div>
            <div className = "paymedio">
             <a href="#"><img className = "img-fluid imgpay" src = {link} alt = {payment}/></a>
                <div className="textpay">
                {textpe}
                </div>
            </div>
          </div>
    </div>
  </a>
)

export default PaymentItem




